<template>
    <div id="home">
        <div class="container-fluid">
            <div class="col-sm-11 col-sm-push-1">
                <h4>{{ title }}</h4>


                <div class="row">
                    <div class="col-sm-12">
                        <v-data-table
                            :headers="headers"
                            :items="latest"
                            hide-actions
                            class="elevation-1"
                        >
                            <template slot="items" slot-scope="props">
                                <td class="text-xs-left">{{ props.item.employee_name }}</td>
                                <td class="text-xs-left">{{ props.item.company_name }}</td>
                                <td class="text-xs-left">{{ props.item.clock_date | formatUxDate }}</td>
                                <td class="text-xs-left">{{ props.item.clock_time | formatUxTime }}</td>
                                <td class="text-xs-left">{{ props.item.clock_in_out | toggleInOut }}</td>
                                <td class="text-xs-center">{{ props.item.reason_name }}</td>
                                <td class="text-xs-center">{{ props.item.project_name }}</td>
                                <td class="text-xs-center">{{ props.item.device_id }}</td>
                                <td class="text-xs-left">{{ props.item.clocking_lat }}</td>
                                <td class="text-xs-left">{{ props.item.clocking_lon }}</td>
                            </template>
                            <template slot="pageText" slot-scope="props">
                                Rows Per Page {{ props.pageStart }} - {{ props.pageStop }} of {{ props.itemsLength }}
                            </template>
                        </v-data-table>                    
                    </div>
                </div>
                <br /><br /><br />
                <div id="shortcut" class="shortcut">
                    <h4>SHORTCUT</h4>
                    <hr>
                    <div class="row">
                        <div class="col-sm-2">
                            <a>
                                <div class="clocking_analysis short_cut_box">
                                    <p>Clocking Analysis</p>
                                </div>
                            </a>
                        </div>

                        <div class="col-sm-2">
                            <a>
                                <div class="clocking_records short_cut_box">
                                    <p>Clocking Records</p>
                                </div>
                            </a>
                        </div>

                        <div class="col-sm-2">
                            <a>
                                <div class="clocking_data_export short_cut_box">
                                    <p>Clock Data Export</p>
                                </div>
                            </a>
                        </div>
                        <div class="col-sm-2">
                            <a><div class="register_staff short_cut_box">
                                <p>Register Staff</p>
                            </div></a>
                        </div>
                        <div class="col-sm-2">
                            <div class="view_register_staff short_cut_box">
                                <p>View Registered Staff</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>   
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import { setTimeout, clearTimeout } from 'timers';

export default {
  data () {
    return {
        title:'LATEST CLOCKING',
        headers: [
                { text: 'FULL NAME', value: 'employee_name' },
                { text: 'COMPANY', value: 'company_name' },
                { text: 'DATE', value: 'clock_date' },
                { text: 'TIME', value: 'clock_time' },
                { text: 'IN/OUT', value: 'clock_in_out' },
                { text: 'REASON', value: 'reason_name' },
                { text: 'PROJECT', value: 'project_name' },
                { text: 'DEVICE ID', sortable: false, value: 'device_id' },
                { text: 'LATITUDE', sortable: false, value: 'clocking_lat' },
                { text: 'LONGTITUDE', sortable: false, value: 'clocking_lon' },
        ],           
        latest: []
    }
  },

  methods: {
    loadLatest() {
        var vm = this

        axios.get(process.env.ROOT_API + '/clock/latest')
        .then(function (response) {
            vm.latest = response.data.rec;
            
        })
        .catch(function (error) {
            vm.status = 'An error occured.' + error;
        })
    },
  },

  created() {
      this.loadLatest()
      
  },

  mounted() {
    // cannot implement the following into loadLatest func, will cause memory leak.
    //let latestTimeOut = setTimeout(this.loadLatest, 1000)  
  },

  destroyed() {
      console.log('detroyed')
      //clearInterval(this.latestTimeOut)
  }
}
</script>

<style>
.shortcut {
    margin-top: 150;
}
</style>



